@import url(https://fonts.googleapis.com/css?family=Raleway:400,,800,900);
body {
  height: 100vh;
  width: 100vw;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: linear-gradient(150deg, hsl(212, 62%, 12%), hsl(212, 66%, 7%));
}

body {
  overflow: hidden;
}

.App {
  min-height: 100vh;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  /* Set it to -1 to disable adding new particles */
  /* z-index: -1; */
}

.button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 200px;
  width: 200px;
  border-radius: 50%;
  border: none;
  background-color: black;
}

.modale {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: fixed;
  top: 100px;
  /* Center on the page */
  left: 0;
  right: 0;
  top: 6em;
  margin-left: auto;
  margin-right: auto;
  max-width: 600px;
  max-height: 80vh;
  overflow: auto;

  background-color: rgba(18, 10, 12, 0.9);
  color: hsla(36, 50%, 95%, 0.9);
  border-radius: 1em;
  padding: 1em;
  z-index: 1;
}

.rocket {
  width: 4em;
  height: auto;
  position: absolute;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.container {
  margin: 0 auto;
  font-family: 'Raleway', sans-serif;
}

.title {
  font-weight: 800;
  color: transparent;
  font-size: 6em;
  background: url('https://phandroid.s3.amazonaws.com/wp-content/uploads/2014/05/rainbow-nebula.jpg')
    repeat;
  background-position: 40% 50%;
  -webkit-background-clip: text;
  background-clip: text;
  text-align: center;
  letter-spacing: -0.5rem;
}

